import kotlinx.browser.window
import kotlinx.coroutines.MainScope
import kotlinx.coroutines.await
import kotlinx.coroutines.launch
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.setState
import styled.css
import styled.styledDiv

suspend fun fetchSource(fileName: String): List<Thirukkural> {
    val sourceUrl = "https://raw.githubusercontent.com/uttran/uttran.github.io/main/private/$fileName.txt"
    val sourceData = window.fetch(sourceUrl).await().text().await()
    val thirukkurals = readSource(sourceData)
    println("version: 2021-01-06.1")
    println("Source: $sourceUrl loaded")
    return thirukkurals
}

external interface AppState: RState {
    var loaded: Boolean
    var thirukkurals: MutableMap<Group, List<Thirukkural>>
    var questionState: QuestionState
}

class App : RComponent<RProps, AppState>() {
    override fun AppState.init() {
        val mainScope = MainScope()
        mainScope.launch {
            val group2Kurals = fetchSource("group2")
            val group3Kurals = fetchSource("group3")
            setState {
                thirukkurals = mutableMapOf()
                thirukkurals[Group.II] = group2Kurals
                thirukkurals[Group.III] = group3Kurals
                questionState = createQuestionState(Group.II, group2Kurals)
                loaded = true
                println("No of athikarams: ${questionState.athikaramState.athikarams.size}")
                println("No of kurals: ${questionState.thirukkurals.size}")
                window.setInterval(timerHandler(), 1000)
            }
        }
    }

    private fun createQuestionState(group: Group, thirukkurals: List<Thirukkural>) = QuestionState(
        selectedGroup = group,
        selectedTopic = Topic.Athikaram,
        thirukkurals = thirukkurals,
        athikaramState = AthikaramState(thirukkurals),
        thirukkuralState = ThirukkuralState(thirukkurals),
        firstWordState = FirstWordState(thirukkurals),
        lastWordState = LastWordState(thirukkurals),
        timerState = TimerState(),
        showAnswer = false
    )

    private fun timerHandler(): () -> Unit = {
        if (state.questionState.timerState.isLive
            && !state.questionState.timerState.isPaused
            && state.questionState.timerState.time > 0) {
            setState {
                questionState.timerState.time--
            }
        }
    }

    override fun RBuilder.render() {
        hotKeys {
            attrs.keyMap = MyKey(arrayOf("s", "n", "p"))
            attrs.handlers = MyHandler {
                when (it.asDynamic()["key"]) {
                    "s" -> onShowAnswerHandler(true)
                    "n" -> onNextClickHandler(state.questionState.selectedTopic)
                    "p" -> onPreviousClickHandler(state.questionState.selectedTopic)
                }
            }
            styledDiv {
                css {
                    classes = mutableListOf("alert alert-primary text-center mb-0")
                }
                +"திருக்குறள் பயிற்சி"
            }
            styledDiv {
                css {
                    classes = mutableListOf("container-lg pl-0 pr-0")
                }
                if (state.loaded) {
                    person {
                        questionState = state.questionState
                        onGroupClick = { group ->
                            setState {
                                if (questionState.selectedGroup != group) {
                                    questionState = createQuestionState(group, thirukkurals[group].orEmpty())
                                }
                            }
                        }
                        onTopicClick = { topic ->
                            setState {
                                if (questionState.selectedTopic != topic) {
                                    questionState.showAnswer = false
                                    questionState.selectedTopic = topic
                                    resetTimer(false)
                                }
                            }
                        }
                        onShowAnswerClick = { onShowAnswerHandler(!state.questionState.showAnswer) }
                        onTimerClick = {
                            setState {
                                val timerState = questionState.timerState
                                when {
                                    timerState.isLive && timerState.time <= 0 -> resetTimer(true)
                                    timerState.isLive && timerState.isPaused -> {
                                        timerState.isPaused = false
                                    }
                                    timerState.isLive && !timerState.isPaused -> {
                                        timerState.isPaused = true
                                    }
                                    else -> {
                                        timerState.isLive = true
                                    }
                                }
                            }
                        }
                        onPreviousClick = {
                            onPreviousClickHandler(questionState.selectedTopic)
                        }
                        onNextClick = {
                            onNextClickHandler(questionState.selectedTopic)
                        }
                    }
                }
            }
        }
    }

    private fun resetTimer(isLive: Boolean) {
        setState {
            questionState.timerState = TimerState(isLive = isLive)
            questionState.athikaramState.clearAnswers()
            questionState.thirukkuralState.clearAnswers()
            questionState.firstWordState.clearAnswers()
            questionState.lastWordState.clearAnswers()
        }
    }

    private fun onShowAnswerHandler(show: Boolean) {
        setState {
            questionState.showAnswer = show
        }
    }

    private fun onNextClickHandler(topic: Topic) {
        onShowAnswerHandler(false)
        when (topic) {
            Topic.Athikaram -> setState { questionState.timerState.count = questionState.athikaramState.goNext() }
            Topic.Porul, Topic.Kural -> setState { questionState.timerState.count = questionState.thirukkuralState.goNext() }
            Topic.FirstWord -> setState { questionState.timerState.count = questionState.firstWordState.goNext() }
            Topic.LastWord -> setState { questionState.timerState.count = questionState.lastWordState.goNext() }
            Topic.AllKurals -> { /* Next button not available*/ }
        }
    }

    private fun onPreviousClickHandler(topic: Topic) {
        onShowAnswerHandler(false)
        when (topic) {
            Topic.Athikaram -> setState { questionState.athikaramState.goPrevious() }
            Topic.Porul, Topic.Kural -> setState { questionState.thirukkuralState.goPrevious() }
            Topic.FirstWord -> setState { questionState.firstWordState.goPrevious() }
            Topic.LastWord -> setState { questionState.lastWordState.goPrevious() }
            Topic.AllKurals -> { /* Previous button not available*/ }
        }
    }
}
